<template>
  <div class="search">
    <p>Search (press esc to cancel)</p>

    <form action>
      <input  v-model="searchText" @keydown="onKeyEsc" type="text" placeholder="search person" class="search__input" />
    </form>
  </div>
</template>

<script>

export default {
  name: 'SearchPanel',
  emits: ['search-text-emit'],

  data() {
    return {
      searchText: '',
    }
  },
  beforeUpdate() {
    this.emitSearchText;
  },
  computed: {
    emitSearchText() {
      this.$emit('search-text-emit', this.searchText);
      return '';
    }

  },
  methods: {
    onKeyEsc(e) {
      if (e.code == 'Escape') {
        this.searchText = '';

      }
    }
  }
}
</script>
<style lang="scss" scoped>
.search {
  padding: 5px 0;
  flex: 0 0;
 
  &__input {
    margin: 5px 0px 0px 0px;
    outline: 1px solid black;
  }
}
</style>